.RelicAdd {
  margin: auto;

  padding: 2rem 0.5rem;

  display: flex;
  flex-direction: column;
  gap: 1rem;

  h3 {
    scroll-snap-align: start;
    padding: 0.5rem;
  }

  .RelicAddSubmit {
    grid-area: submit;
    display: flex;
    justify-content: center;

    input {
      border: none;
      border-radius: 1rem;

      width: 100%;
      height: 3rem;

      background-color: var(--theme-color);
      color: white;
      font-size: 1rem;
      font-weight: bolder;
    }
    input:hover {
      opacity: 0.5;
    }
  }
}
@media (min-width: 48rem) {
  .RelicAdd {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 6rem);
    grid-template-areas:
      "slot sub"
      "set sub"
      "main sub"
      "submit sub";
  }
}
